html {
  scroll-behavior: smooth;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Lato, sans-serif;
}

main {
  width: 80%;
  margin: 0 auto;
  box-shadow: 0 0 50px 2px #00000059;
}

.container {
  width: 80%;
  margin: 0 auto;
}

.title__header {
  text-transform: uppercase;
  color: #353738;
  text-align: center;
  margin-top: 35px;
  font-size: 18px;
}

.title__subheader {
  color: #848789;
  text-align: center;
  margin-top: 35px;
  font-size: 18px;
}

.title__subheader-fz16 {
  font-size: 16px;
}

.header {
  height: 100vh;
  background: url("First_bg.67a7c5a8.jpg") center / cover no-repeat;
  position: relative;
}

.header__links__wrapper {
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding-top: 35px;
  display: flex;
}

.header__burger__wrapper {
  z-index: 5;
  width: 100%;
  padding-top: 15px;
  padding-left: 15px;
  padding-right: 15px;
  position: fixed;
}

.header__link {
  text-transform: uppercase;
  color: #353738;
  margin: 0 20px;
  font-size: 13px;
  font-weight: bold;
  text-decoration: none;
}

.header__link:hover {
  color: #848789;
}

.header .burger {
  justify-content: center;
  align-items: center;
  display: none;
}

.header .menu-btn {
  width: 50px;
  height: 50px;
  background-color: #fff;
  border: 5px solid #71b699;
  border-radius: 50%;
  flex: none;
  display: block;
  position: relative;
}

.header .menu-btn span, .header .menu-btn span:before, .header .menu-btn span:after {
  width: 20px;
  height: 2px;
  background-color: #222;
  margin-top: -1px;
  margin-left: -10px;
  position: absolute;
  top: 50%;
  left: 50%;
}

.header .menu-btn span:before, .header .menu-btn span:after {
  content: "";
  transition: all .2s;
  display: block;
}

.header .menu-btn span:before {
  transform: translateY(-5px);
}

.header .menu-btn span:after {
  transform: translateY(5px);
}

.header .menu-btn_active {
  background-color: #71b699;
}

.header .menu-btn_active span:before {
  width: 10px;
  transform-origin: 0 100%;
  transform: rotate(-35deg);
}

.header .menu-btn_active span:after {
  width: 10px;
  transform-origin: 0 0;
  transform: rotate(35deg);
}

.header .menu-nav a {
  color: #222;
  text-transform: uppercase;
  margin-right: 10px;
  font-size: 12px;
  font-weight: 100;
  text-decoration: none;
  transition: all 1s;
  display: inline-block;
}

.header .menu-nav {
  width: 100%;
  transform-origin: 100%;
  opacity: 0;
  visibility: hidden;
  height: 50px;
  background-color: #fff;
  border-top-left-radius: 50px;
  border-bottom-left-radius: 50px;
  justify-content: center;
  align-items: center;
  margin-right: -25px;
  padding-left: 25px;
  padding-right: 25px;
  line-height: 50px;
  transition: all .2s;
  display: flex;
  transform: scaleX(0)translateX(0%);
}

.header .menu-nav_active {
  opacity: 1;
  visibility: visible;
  transform: scaleX(1)translateX(0%);
}

.aboutUs {
  height: 470px;
  background-color: #f7f7f7;
  scroll-margin-top: 150px;
}

.aboutUs__wrapper {
  height: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 12vw;
  display: flex;
}

.works__wrapper {
  aspect-ratio: 1 / 1;
  grid-template-columns: repeat(3, 33.3333%);
  display: grid;
}

.works__wrapper .w1 {
  grid-area: 1 / 1 / 2 / 3;
}

.works__wrapper .w4 {
  grid-area: 2 / 2 / 3 / 4;
}

.works__wrapper .imgOnly img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.works__wrapper .card {
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.works__wrapper .card__wrapper {
  height: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 30px;
  display: flex;
}

.works__wrapper .card__wrapper img {
  margin-top: 30px;
}

.works__wrapper .card__img {
  width: 100%;
  margin-top: 30px;
}

.twitter {
  height: 470px;
  background-color: #f7f7f7;
}

.twitter__wrapper {
