}

.shop {
  scroll-margin-top: 70px;
}

.shop__wrapper {
  grid-template-columns: repeat(6, 16.6667%);
  display: grid;
}

.shop__card {
  position: relative;
}

.shop__card__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.shop__card__overlay {
  height: 100%;
  width: 100%;
  opacity: 0;
  background-color: #71b699bb;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: all .5s;
  display: flex;
  position: absolute;
  inset: 0;
  box-shadow: inset 0 0 30px #0000004d;
}

.shop__card__overlay-txt {
  color: #fff;
  text-transform: uppercase;
  margin-top: 20px;
  font-size: 14px;
}

.shop__card:hover .shop__card__overlay {
